.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.Jumbotron {
  line-height: 50px;
  height: 300px;
  margin-top: 5%;
  margin-left: 10%;
  margin-right: 10%;
  margin-bottom: 2rem;
  border-radius: .3rem;
  background-image: url('./testpubg.jpg');
  background-size: cover ;
  color: white;
  font-size: 25px;
  font-weight: bold;
  text-shadow: 2px 2px 2px black;
  overflow: hidden;
  text-overflow: ellipsis;
}
.Jumbo-title {
  margin: auto;
  margin-top: 20px;
  line-height: 150px;
}

.place {
  margin-top: 15px;
  margin-bottom: 15px;
  margin-left: 15%;
  margin-right: 15%;
}

.row {
  margin-top: 15px;
  margin-bottom: 15px;
  box-sizing: border-box;
}

.col-md-4 {
  width: 30%;
  margin-left: 10px;
  margin-right: 10px;
  margin-bottom: 20px;
  background-color:  #0b2230;
  border-radius: 10px;
  border: 2px solid rgb(20, 20, 88);
  box-shadow: 3px 3px 10px black;
  font-size: 15px;
  overflow: hidden;
}
.col-md-4-img {
  margin-top: 10px;
  margin-bottom: 10px;
}

.col-md-4-title {
  font-size: 20px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
