$maincolor : #ff0000;

body {
    background-color: #1b2838;
    color: white;
    margin: 0;
    padding: 0;
}
.red{
    color: $maincolor;
}

.rerow {
    padding: 0px 0px 0px 0px;
    margin-top: 15px;
    margin-left: 10%;
    margin-right: 10%;
    border: 1px solid rgb(255, 255, 255);
    box-shadow: 3px 3px 10px black;

}

.col-md-6 {
    padding: 0px 0px 0px 0px;
    width: 100%;
}

.selbody {
    margin-bottom: 20px;
    margin-left: 10%;
    margin-right: 10%;
    font-size: 15px;
    text-shadow: 2px 2px 2px black;
}

.seltitle {
    padding-top: 20px;
    margin-bottom: 25px;
}

.selprice {
    font-size: 20px;
    font-weight: bold;
}
